import { Typography, Image } from "antd";
export function NewsContent (props) {
    const { Title, Paragraph, Text } = Typography;

    return(
        <Typography>
            <Title id="title">{props.newscase.title}</Title>
            <blockquote id="subtitle">{props.newscase["sub-title"]}</blockquote>

            {props.newscase.content.map((e,i)=>{
                if(e.hasImage){
                    return (
                        <>
                        <Paragraph id={"news-content-"+i} key={"news-content-"+i}>{e.text}</Paragraph>
                        <Image
                            width={900}
                            src={process.env.PUBLIC_URL+e.ImageURL}
                            preview={false}
                            key={'img-'+i}
                            if={'img-'+i}
                            style={{
                                marginBottom: 10
                            }}
                        />
                        </>
                    );
                }
                return (
                    <Paragraph id={"news-content-"+i} key={"news-content-"+i}>{e.text}</Paragraph>
                );
            })}
        </Typography>
    );
}