import { useState } from "react";
import {Layout, Col, Row} from 'antd';
import 'antd/dist/reset.css';
import './styles/App.css';

/** React components */
import { NavBar } from "./components/nav";
import { NewsContent } from "./components/content";

/** Cases */
import caseArticle from './data/case.json';

function App() {
  const [selectedCase, setSelectedCase] = useState(1);
  //src={process.env.PUBLIC_URL+props.question.pictureURL}

  const { Header, Content } = Layout;
  const activeArticle = caseArticle.cases[selectedCase];
  const newStyle = {
    backgroundColor: '#fff',
    padding: 20
  };
  return (
    <div className="App">
      <Layout className="mainContainer">
      <Header style={{height: 40}}>
        <NavBar
          selectedCase={selectedCase}
          setSelectedCase={setSelectedCase}
        />
      </Header>
      <Content className='vastContainer' style={newStyle}>
        <Row>
          <Col span={12} offset={6}>
            <NewsContent 
              newscase={activeArticle}
            />
          </Col>
        </Row>
        
      </Content>
     </Layout>
    </div>
  );
}

export default App;
